import { CommonUtil } from './common-util';
import { ConditionalValidator } from './wfe-conditional-validator';
import { WFEConditionalExecuter } from './wfe-conditional-query-executer-util';
import { AppDataService } from '../models/app-data/app-data.service';
import { ApiDataService } from '../models/api-data/api-data.service';
import { Inject, Injectable } from '@angular/core';
@Injectable({
    providedIn: 'root',
})
export class ConditionalUtil {

    private commonUtils: CommonUtil;
    private wfeConditionalExecuter: WFEConditionalExecuter;
    private conditionalValidator: ConditionalValidator;
    isSingleCondition = false;

    constructor(@Inject('isSingleCondition') isSingleCondition: boolean, private appDataService?: AppDataService, apiDataService?: ApiDataService) {
        this.commonUtils = new CommonUtil();
        this.wfeConditionalExecuter = new WFEConditionalExecuter(appDataService, apiDataService);
        this.conditionalValidator = new ConditionalValidator(appDataService);
        this.isSingleCondition = isSingleCondition;
    }

    checkCustomClass(fieldData: any, apiKey: string, value: any, data = {}) {

        // conditional custom class checking for fields
        if (fieldData.custom_class_condition && fieldData.custom_class_condition.length > 0) {
            let customClassValue = false;
            for (const customClassCondition of fieldData.custom_class_condition) {
                customClassValue = this.wfeConditionalExecuter.evaluateConditionQuery(
                    customClassCondition.query,
                    customClassCondition.condition, '', {}, fieldData);
                if (customClassValue) {
                    break;
                }
            }
            return customClassValue;
        }

        // conditional custom class checking for fields
        if (fieldData.custom_class_condition) {
            if (this.isSingleCondition) {
                if (fieldData.custom_class_condition.field === apiKey) {
                    return this.conditionalValidator.checkConditionalValidation(
                        this.commonUtils.getID(fieldData.custom_class_condition.field_values), value,
                        fieldData.custom_class_condition.comparision_operator);
                }
            } else {
                return this.wfeConditionalExecuter.evaluateConditionQuery(
                    fieldData.custom_class_condition.query,
                    fieldData.custom_class_condition.condition, apiKey, data, fieldData);
            }
        }
        return undefined;
    }

    checkBootstrapClass(fieldData: any, apiKey: string, value: any, data = {}) {

        // conditional Bootstrap class checking for fields
        if (fieldData.bootstrap_class_condition && fieldData.bootstrap_class_condition.length > 0) {
            let customClassValue = false;
            for (const customClassCondition of fieldData.bootstrap_class_condition) {
                customClassValue = this.wfeConditionalExecuter.evaluateConditionQuery(
                    customClassCondition.query,
                    customClassCondition.condition, '', {}, fieldData);
                if (customClassValue) {
                    break;
                }
            }
            return customClassValue;
        }

        // conditional Bootstrap class checking for fields
        if (fieldData.bootstrap_class_condition) {
            if (this.isSingleCondition) {
                if (fieldData.bootstrap_class_condition.field === apiKey) {
                    return this.conditionalValidator.checkConditionalValidation(
                        this.commonUtils.getID(fieldData.bootstrap_class_condition.field_values), value,
                        fieldData.bootstrap_class_condition.comparision_operator);
                }
            } else {
                return this.wfeConditionalExecuter.evaluateConditionQuery(
                    fieldData.bootstrap_class_condition.query,
                    fieldData.bootstrap_class_condition.condition, apiKey, data, fieldData);
            }
        }
        return undefined;
    }

    checkMandatory(fieldData: any, apiKey: string, value: any, data = {}) {

        // conditional mandatory checking for section or blocks
        if (fieldData && fieldData.conditional_mandatory && fieldData.conditional_mandatory.length > 0) {
            let mandatoryValue;
            for (const mandatoryCondition of fieldData.conditional_mandatory) {
                if (!mandatoryCondition.query.trim()) {
                    continue;
                }
                mandatoryValue = this.wfeConditionalExecuter.evaluateConditionQuery(
                    mandatoryCondition.query,
                    mandatoryCondition.condition);
                if (mandatoryValue) {
                    break;
                }
            }
            return mandatoryValue;
        }

        // conditional mandatory checking for fields
        if (fieldData?.is_mandatory === 2) {
            if (fieldData.mandatory_condition) {
                if (this.isSingleCondition) {
                    if (fieldData.mandatory_condition.field === apiKey) {
                        return this.conditionalValidator.checkConditionalValidation(
                            this.commonUtils.getID(fieldData.mandatory_condition.field_values), value,
                            fieldData.mandatory_condition.comparision_operator);
                    }
                } else {
                    return this.wfeConditionalExecuter.evaluateConditionQuery(
                        fieldData.mandatory_condition.query,
                        fieldData.mandatory_condition.condition, apiKey, data);
                }
            }
        }
        return undefined;
    }

    checkStepDependency(dependencyCondition) {
        return this.wfeConditionalExecuter.evaluateConditionQuery(
            dependencyCondition.query,
            dependencyCondition.condition);
    }

    checkEditable(fieldData: any, apiKey: string, value: any, data = {}) {

        // conditional editable checking for section or blocks
        if (fieldData.conditional_editable && fieldData.conditional_editable.length > 0) {
            let editableValue;
            for (const editableCondition of fieldData.conditional_editable) {
                if (!editableCondition.query.trim()) {
                    continue;
                }
                editableValue = this.wfeConditionalExecuter.evaluateConditionQuery(
                    editableCondition.query,
                    editableCondition.condition);
                if (editableValue) {
                    break;
                }
            }
            return editableValue;
        }

        // conditional editable checking for fields
        if (fieldData.is_editable === 2) {
            if (fieldData.editable_condition) {
                if (this.isSingleCondition) {
                    if (fieldData.editable_condition.field === apiKey) {
                        return this.conditionalValidator.checkConditionalValidation(
                            this.commonUtils.getID(fieldData.editable_condition.field_values), value,
                            fieldData.editable_condition.comparision_operator);
                    }
                } else {
                    return this.wfeConditionalExecuter.evaluateConditionQuery(
                        fieldData.editable_condition.query,
                        fieldData.editable_condition.condition, apiKey, data);
                }
            }
        }
        return undefined;
    }

    checkVisibility(fieldData: any, apiKey: string, value: any, data = {}) {
        // conditional visible checking for section or blocks
        if (fieldData.conditional_visible && fieldData.conditional_visible.length > 0) {
            let visibleValue;
            for (const visibleCondition of fieldData.conditional_visible) {
                if (!visibleCondition.query.trim()) {
                    continue;
                }
                visibleValue = this.wfeConditionalExecuter.evaluateConditionQuery(
                    visibleCondition.query,
                    visibleCondition.condition);
                if (visibleValue) {
                    break;
                }
            }
            return visibleValue;
        }

        // conditional visible checking for fields
        if (fieldData.is_display === 2) {
            if (fieldData.display_condition) {
                if (this.isSingleCondition) {
                    if (fieldData.display_condition.field === apiKey) {
                        return this.conditionalValidator.checkConditionalValidation(
                            this.commonUtils.getID(fieldData.display_condition.field_values), value,
                            fieldData.display_condition.comparision_operator);
                    }
                } else {
                    return this.wfeConditionalExecuter.evaluateConditionQuery(
                        fieldData.display_condition.query,
                        fieldData.display_condition.condition, apiKey, data);
                }
            }
        }
        return undefined;
    }

    conditionalValue(fieldData: any, apiKey: string, value: any, currentValue: string) {
        const {
            is_conditional_value,
            conditional_value
        } = fieldData;
        let fieldValue = currentValue;
        if (is_conditional_value && is_conditional_value === 1
            && conditional_value && Array.isArray(conditional_value)) {
            for (const conditionalValue of conditional_value) {
                const {
                    // step,
                    field,
                    field_values,
                    comparision_operator,
                    value_option,
                    value_option_value,
                    value_other,
                    value_other_value,
                    value_step,
                    value_step_id,
                    value_step_field,
                    value_step_field_value
                } = conditionalValue;

                if (field === apiKey) {
                    const checkCondition = this.conditionalValidator.checkConditionalValidation(
                        field_values, value,
                        comparision_operator);
                    if (checkCondition) {
                        if (value_option === 1) {
                            fieldValue = value_option_value;
                        } else if (value_other === 1) {
                            fieldValue = value_other_value;
                        } else {
                            fieldValue = value_step_field_value;
                        }
                    }
                }
                if (value_step === 1) {
                    const conditionFieldVal = this.appDataService.getFieldDataByFieldId(field);
                    const checkCondition = this.conditionalValidator.checkConditionalValidation(
                        field_values, conditionFieldVal,
                        comparision_operator);
                    if (checkCondition && value_step_field_value == 'DYNAMIC') {
                        const valueFieldVal = this.appDataService.getFieldDataByFieldId(value_step_field);
                        fieldValue = valueFieldVal;
                    }
                }
            }

        }
        return fieldValue;
    }

    checkClass(data, blockData) {
        // conditional class checking for section or blocks
        if (blockData.custom_class_condition && blockData.custom_class_condition.condition.length > 0) {
            let classValue = false;
            classValue = this.wfeConditionalExecuter.evaluateConditionQuery(
                blockData.custom_class_condition.query,
                blockData.custom_class_condition.condition, '', data);
            return classValue;
        }
    }

    evaluteBootstrapCondition(field) {
        let bootstrapClassCondition = field && field.hasOwnProperty('bootstrap_class_condition') && field['bootstrap_class_condition'] ? field['bootstrap_class_condition'] : {};
        let isConditionValid = true;
        if (bootstrapClassCondition && bootstrapClassCondition.hasOwnProperty('query') && bootstrapClassCondition['query'] != '') {
            isConditionValid = this.checkBootstrapClass(field, String(field.unique_id), '');
        }
        return isConditionValid;
    }
}
