import { CommonModule } from '@angular/common';
import { AfterViewInit, Component, ElementRef, HostListener, OnDestroy, ViewChild } from '@angular/core';
// import { SvgIconComponent } from '../svg-icon/svg-icon.component';
import { HttpClient } from '@angular/common/http';
import { Input, forwardRef } from '@angular/core';
import { ChildCondition } from '../../models/childCondition.model';
import { CeeButtonComponent } from '../../field-components/cee-button/cee-button.component';
import { BlockRendererComponent } from '../block-renderer/block-renderer.component';
import { StepRendererComponent } from '../step-renderer/step-renderer.component';
import { SharedEventsServiceService } from '../../services/shared-events-service.service';
import { CommonUtil } from '../../utils/common-util';
import { Subscription } from 'rxjs/dist/types';
import { LoaderService } from '../../services/loader-service';

@Component({
  selector: 'cee-drawer-renderer',
  standalone: true,
  imports: [forwardRef(() => StepRendererComponent), forwardRef(() => BlockRendererComponent), CeeButtonComponent, CommonModule],
  templateUrl: './drawer-renderer.component.html',
  styleUrls: ['./drawer-renderer.component.scss'],
})
export class DrawerComponent implements AfterViewInit, OnDestroy {
  @ViewChild('modal') modal!: ElementRef<HTMLDialogElement>;
  @ViewChild('dial') dial!: ElementRef<HTMLDialogElement>;
  autoPreviewConfigPath = localStorage.getItem('ENABLE_AUTO_PREVIEW')?.toLowerCase() == "true" && localStorage.getItem('AUTO_PREVIEW_CONFIG_PATH') ? localStorage.getItem('AUTO_PREVIEW_CONFIG_PATH') : 'assets/config';
  configBlockPath = this.autoPreviewConfigPath + '/application_blocks/application_blocks_';
  @Input() declare stepId: any;
  @Input() templateId: any;
  @Input() declare rootData: any;
  @Input() rowData: any;
  @Input() declare data: any;
  variableObj: any = {};
  lastClickedElement: EventTarget | null = null;
  mandatoryCondition: ChildCondition = {
    value: false,
    fields: [],
    exceptionFields: [],
    isActive: false,
    override: false,
    overrideAll: false
  };

  editableCondition: ChildCondition = {
    value: false,
    fields: [],
    exceptionFields: [],
    isActive: false,
    override: false,
    overrideAll: false
  };

  visibleCondition: ChildCondition = {
    value: false,
    fields: [],
    exceptionFields: [],
    isActive: false,
    override: false,
    overrideAll: false
  };
  startSession: any;
  modalButtons: any = [];
  commonUtil: CommonUtil;
  private isLoadingState = false;
  private loadingSubscription?: Subscription;

  constructor(private sharedEventsService: SharedEventsServiceService, private loaderService: LoaderService) {
    this.startSession = this.data?.startSession;
    let jsonval = null;
    this.data?.modal && this.data?.modal?.buttons.forEach((button, i) => {
        jsonval = {
            field_label: button.label,
            unique_id: "modal_button_" + i,
            is_mandatory: 0,
            is_editable: 1,
            is_display: 1,
            is_conditional_value: 0,
            field_type: "Button",
            validation_messages: [],
            validation_regex: "",
            additional_parameters: [],
            api_key: "",
            request_api_key: "",
            response_api_key: "",
            tooltip: "",
            possible_values: [],
            event_list: button.event_list,
        };

        this.modalButtons.push(jsonval);
    });
    this.sharedEventsService.closeDrawerOnClickExternalUrl.subscribe(res => {
      if (res) {
        this.modal.nativeElement.close();
      }
    });
    this.commonUtil = new CommonUtil();
  }
  ngOnInit() {
    // Subscribe to loading state
    this.loadingSubscription = this.loaderService.isLoading.subscribe(
      loading => this.isLoadingState = loading
    );
  }
  // Close modal
  closeModal() {
    localStorage.setItem('isDrawerOpen', '0');
    if (this.modal.nativeElement && this.modal.nativeElement.open) {
      this.modal.nativeElement.close();
      this.commonUtil.moveMatDialogToBody();
      this.sharedEventsService.modelEmitter.emit({ data: { stepId: "", config: { disableClose: true } }, id: ``, panelClass: `` })
    }
  }
  
  // Listen for ESC key press
  @HostListener('document:keydown.escape', ['$event'])
  onEscapeKey(event: KeyboardEvent) {
    this.onDialogClose();
  }
  
  // Handle dialog close event
  onDialogClose() {
    if (this.modal.nativeElement && this.modal.nativeElement.open) {
      this.modal.nativeElement.close();
      this.commonUtil.moveMatDialogToBody();
      this.sharedEventsService.modelEmitter.emit({ data: { stepId: "", config: { disableClose: true } }, id: ``, panelClass: `` })
    }
  }

  openModal() {
    localStorage.setItem('isDrawerOpen', '1');
    if (this.modal.nativeElement && !this.modal.nativeElement.open) {
      this.lastClickedElement = null;
      this.modal.nativeElement.showModal();
    }
  }

  @HostListener('document:click', ['$event'])
  handleDocumentClick(event: MouseEvent): void {
    // Check if loading - if yes, don't close modal
    if (this.isLoadingState) {
      return;
    }
    const targetElement = event.target as HTMLElement,
      isModalOpen = this.modal?.nativeElement?.open,
      clickedOutside = this.dial.nativeElement.contains(targetElement),
      shouldDismiss = this.data.config.dismissModal === 0,
      //Check if target has class and to resist dropdown clicks as it renders outside component's DOM tree
      targetHasClass = targetElement.classList.contains('dropdown-item');

    if (isModalOpen && !clickedOutside && !targetHasClass && shouldDismiss) {
      this.modal.nativeElement.close();
      this.commonUtil.moveMatDialogToBody();
      this.sharedEventsService.modelEmitter.emit({ data: { stepId: "", config: { disableClose: true } }, id: ``, panelClass: `` })
    }
  }

  ngAfterViewInit() {
    // Only after the view is initialized can we access the nativeElement
    if (this.modal && this.modal.nativeElement && this.stepId) {
      this.modal.nativeElement.addEventListener('close', this.onDialogClose.bind(this));
      this.openModal();
    }
  }

  // Clean up event listeners to prevent memory leaks
  ngOnDestroy() {
    localStorage.setItem('isDrawerOpen', '0');
    if (this.modal && this.modal.nativeElement) {
      this.modal.nativeElement.removeEventListener('close', this.onDialogClose);
    }
     if (this.loadingSubscription) {
      this.loadingSubscription.unsubscribe();
    }
  }

  isHTML(value: string) {
    const regex = /(<([^>]+)>)/ig;
    return regex.test(value);
  }
}