import { HttpClient } from '@angular/common/http';
import { Component, EventEmitter, Inject, NgZone, Injector, Input, OnInit, Output, ViewEncapsulation, forwardRef } from '@angular/core';
import { MatSnackBar as MatSnackBar } from '@angular/material/snack-bar';
import { MatDialog as MatDialog, MatDialogRef as MatDialogRef, MAT_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ChildCondition } from '../../models/childCondition.model';
import { CeeButtonComponent } from '../../field-components/cee-button/cee-button.component';
import { BlockRendererComponent } from '../block-renderer/block-renderer.component';
import { StepRendererComponent } from '../step-renderer/step-renderer.component';
import { CommonModule } from '@angular/common';
import {MatDialogModule} from '@angular/material/dialog';
@Component({
    selector: 'lib-modal-renderer',
    templateUrl: './modal-renderer.component.html',
    styleUrls: ['./modal-renderer.component.scss'],
    standalone: true,
    imports: [forwardRef(() => StepRendererComponent), forwardRef(() => BlockRendererComponent), MatDialogModule, CeeButtonComponent, CommonModule],
    // tslint:disable-next-line: no-host-metadata-property
    host: { class: 'stepModal' },
    encapsulation: ViewEncapsulation.None,
})
export class ModalRendererComponent {
    autoPreviewConfigPath = localStorage.getItem('ENABLE_AUTO_PREVIEW')?.toLowerCase() == "true" && localStorage.getItem('AUTO_PREVIEW_CONFIG_PATH') ? localStorage.getItem('AUTO_PREVIEW_CONFIG_PATH') : 'assets/config';
    configBlockPath = this.autoPreviewConfigPath + '/application_blocks/application_blocks_';
    @Input() declare stepId: any;
    @Input() templateId: any;
    @Input() declare rootData: any;
    @Input() rowData: any;
    mandatoryCondition: ChildCondition = {
        value: false,
        fields: [],
        exceptionFields: [],
        isActive: false,
        override: false,
        overrideAll: false
    };

    editableCondition: ChildCondition = {
        value: false,
        fields: [],
        exceptionFields: [],
        isActive: false,
        override: false,
        overrideAll: false
    };

    visibleCondition: ChildCondition = {
        value: false,
        fields: [],
        exceptionFields: [],
        isActive: false,
        override: false,
        overrideAll: false
    };
    startSession: any;
    modalButtons: any = [];
    constructor(
        public dialogRef: MatDialogRef<ModalRendererComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any) {
        if (data.config && data.config.disableClose) {
            dialogRef.disableClose = data.config.disableClose;
        }
        this.startSession = data.startSession;
        if (this.startSession === false) {
            dialogRef.disableClose = true;
        }
        let jsonval = null;
        data.modal && data.modal.buttons.forEach((button, i) => {
            jsonval = {
                field_label: button.label,
                unique_id: "modal_button_" + i,
                is_mandatory: 0,
                is_editable: 1,
                is_display: 1,
                is_conditional_value: 0,
                field_type: "Button",
                validation_messages: [],
                validation_regex: "",
                additional_parameters: [],
                api_key: "",
                request_api_key: "",
                response_api_key: "",
                tooltip: "",
                possible_values: [],
                event_list: button.event_list,
            };

            this.modalButtons.push(jsonval);
        });
    }

    closeDialog(): void {
        this.dialogRef.close();
    }
    isHTML(value: string) {
        const regex = /(<([^>]+)>)/ig;
        return regex.test(value);
    }
    toggleModalCssClass(cssclass) {
        const modalWrapper = window.document.querySelector('.cdk-overlay-pane');
        if (modalWrapper) {
            modalWrapper.classList.add('modal-wrapper');
            modalWrapper.classList.add(cssclass);
        }
    }
}
