import { Component, OnInit, Input, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { AppDataService } from '../../models/app-data/app-data.service';
import { SharedEventsServiceService } from '../../services/shared-events-service.service';
import { ApiDataService } from '../../models/api-data/api-data.service';
import { WfeStepLoaderService } from '../../services/wfe-step-loader.service';
import { MatSnackBar as MatSnackBar, MAT_SNACK_BAR_DATA as MAT_SNACK_BAR_DATA } from '@angular/material/snack-bar';
import { MatProgressBarModule } from '@angular/material/progress-bar';
import { CommonModule } from '@angular/common';
@Component({
    selector: 'app-snackbar-renderer',
    templateUrl: './snackbar-renderer.component.html',
    styleUrls: ['./snackbar-renderer.component.scss'],
    standalone: true,
    imports:[MatProgressBarModule, CommonModule]
})
export class SnackbarRendererComponent {
    @Input() declare stepId: any;
    @Input() templateId: any;
    @Input() declare rootData: any;
    @Input() rowData: any;
    
    constructor(
        @Inject(MAT_SNACK_BAR_DATA) public data: any,
        private http: HttpClient,
        private router: Router,
        private appDataService: AppDataService,
        public apiDataService: ApiDataService,
        public snackBar: MatSnackBar,
        private sharedEventsService: SharedEventsServiceService,
        private wfeStepLoaderService: WfeStepLoaderService) {

    }
}
