import { CommonModule, NgSwitch } from '@angular/common';
import { Component, OnInit, Inject, AfterViewInit } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { MatDialogModule } from '@angular/material/dialog';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { MatDialogRef as MatDialogRef, MAT_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/dialog';
import { NgxImageZoomModule } from 'ngx-image-zoom';
import { DomSanitizePipe } from '../../pipes/dom-sanitize.pipe';

@Component({
    selector: 'lib-alert-modal',
    templateUrl: './alert-modal.component.html',
    styleUrls: ['./alert-modal.component.scss'],
    standalone: true,
    imports: [NgSwitch, MatFormFieldModule, MatInputModule, CommonModule, MatDialogModule, NgxImageZoomModule, DomSanitizePipe, FormsModule]
})
export class AlertModalComponent implements AfterViewInit {

    constructor(
        public dialogRef: MatDialogRef<AlertModalComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any) {
        // console.log(data);
        if (data.config && data.config.disableClose) {
            dialogRef.disableClose = data.config.disableClose;
        }
    }

    ngAfterViewInit(): void {
        if (this.data?.class) {
            const dialogContainer = document.querySelector('.mat-mdc-dialog-container');
            dialogContainer?.classList.add(this.data.class);
        }
    }

    onPostiveClick(): void {
        this.dialogRef.close(true);
    }

    onNegativeClick(): void {
        this.dialogRef.close(false);
    }
}
