export class SessionKeyUtil {
    constructor() {}

    generateSessionKey(fieldData) {
        return fieldData.unique_id + ':::' + fieldData.api_key;
    }

    getFieldNameFromSessionKey(key: string) {
        const value = key.split(':::');
        if (value.length > 1) {
            return value[0];
        } else {
            return key;
        }
    }

    getApiKeyFromSessionKey(key: string) {
        const value = key.split(':::');
        if (value.length > 1) {
            return value[1];
        } else {
            return key;
        }
    }

    getApiKeyValueFromSession(key: string) {
        const session = window.sessionStorage;
        for (const data in session) {
            if (key === this.getApiKeyFromSessionKey(data)) {
                return { key, value: session[data]};
            }
        }
    }

    getSessionKeyUsingUniqueId(key: string) {
        const session = window.sessionStorage;
        // tslint:disable-next-line: forin
        for (const data in session) {
            if (key === this.getFieldNameFromSessionKey(data)) {
                return data;
            }
        }
    }

    getSessionKeyFromApiKey(key: string) {
        const session = window.sessionStorage;
        for (const data in session) {
            if (this.getApiKeyFromSessionKey(data) === key) {
                return data;
            }
        }
        return key;
    }
}
