import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent } from '@angular/common/http';
import { Observable } from 'rxjs';
import { UserDataHandlerService } from './user-data-handler.service';

@Injectable()
export class BearerTokenServiceService implements HttpInterceptor {

  tokenObj: Object = {}
  constructor(private storageService: UserDataHandlerService) { }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    this.tokenObj['bearerToken'] = this.storageService.getFieldData('access_token'.toLowerCase());
    request = request.clone({
      setHeaders: {
        Authorization: "Bearer " + this.tokenObj['bearerToken']
      }
    });
    return next.handle(request);
  }

}
