export class EmptyUtil {
    /**
     * This function checks empty be it array, string , null ,undefined
     * add or remove cases if needed
     * @param value any
     */
    isEmpty(value) {
        if (Array.isArray(value) ) {
            if (value.length > 0) {
                return false;
            } else {
                return true;
            }
        } else {

            switch (value) {
                case '':
                case null:
                case false:
                case undefined:
                case typeof (value) == 'undefined':
                    return true;
                default:
                    return false;
            }
        }
    }
}
