import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class LoaderService {
    public skipLoading: boolean = false;
    public loadingStack: number = 0;
    public isLoading = new BehaviorSubject(false);
    loader = this.isLoading.asObservable();
    public loadingTextSet: BehaviorSubject<string> = new BehaviorSubject('');
    loadingTextGet = this.loadingTextSet.asObservable();
    public isLoading$ = new BehaviorSubject<boolean>(false);
    constructor() { }
}
