import { AppDataState } from '../models/app-data/app-data.store';
import { CommonUtil } from './common-util';

export class ConditionalValidator {
    commonUtils: CommonUtil;
    constructor(private appDataService?:AppDataState) {
        this.commonUtils = new CommonUtil();
    }

    removeNonNumeric(str: string | null | undefined): string {
        const input = String(str || "").trim();
        if (input === "") {
            return "0";
        }        
        return (str ?? "").toString().replace(/[^0-9.]/g, "");
    }

    checkConditionalValidation(val1: any, val2: any, operator: string): boolean {
        let val11 = this.appDataService.getFieldDataByFieldId(val1);
        let val22 = this.appDataService.getFieldDataByFieldId(val2);
        val1 = val11 && val11 != undefined ? val11 : this.commonUtils.getID(val1);
        val2 = val22 && val22 != undefined ? val22 : this.commonUtils.getID(val2);
        const op = operator.trim().toUpperCase();
        if (op =="GT" || op =="LT" || op =="GTE" || op =="LTE"){
            val1 = this.removeNonNumeric(val1);
            val2 = this.removeNonNumeric(val2);
        }

        switch (op) {
            case 'E':
                // removing type checking
                // and making all the values to lowercase
                // according to the meeting on 31/08/2020
                // tslint:disable-next-line: triple-equals

                // Commented the lowercase of all values as discussed with Abhishek on CI-708
                // val1 = typeof (val1) === 'string' ? val1.toLowerCase() : val1;
                // val2 = typeof (val2) === 'string' ? val2.toLowerCase() : val2;
                if (val1 == val2) {
                    return true;
                } else {
                    return false;
                }
            case 'NE':
                // removing type checking
                // and making all the values to lowercase
                // according to the meeting on 31/08/2020
                // tslint:disable-next-line: triple-equals

                // Commented the lowercase of all values as discussed with Abhishek on CI-708
                // val1 = typeof (val1) === 'string' ? val1.toLowerCase() : val1;
                // val2 = typeof (val2) === 'string' ? val2.toLowerCase() : val2;
                if (val1 != val2) {
                    return true;
                } else {
                    return false;
                }

            case 'GT':
                if (Number(val1) < Number(val2)) {
                    return true;
                } else {
                    return false;
                }

            case 'LT':
                if (Number(val1) > Number(val2)) {
                    return true;
                } else {
                    return false;
                }

            case 'GTE':
                if (Number(val1) <= Number(val2)) {
                    return true;
                } else {
                    return false;
                }

            case 'LTE':
                if (Number(val1) >= Number(val2)) {
                    return true;
                } else {
                    return false;
                }
            case 'NOT IN':
            case 'IN':
                if (val2 !== undefined) {
                    if (val2.includes(val1)) {
                        return op === 'NOT IN' ? false : true;
                    } else {
                        return op === 'NOT IN' ? true : false;
                    }
                } else {
                    return false;
                }
            case 'FLGT':
                if (val2 !== undefined) {
                    if (val2.length > val1) {
                        return true;
                    }
                }
                return false;
            case 'FLLT':
                if (val2 && val2.length < val1) {
                    return true;
                } else {
                    return false;
                }
            case 'FLE':
                if (val2 !== undefined) {
                    if (val2.length == val1) {
                        return true;
                    }
                }
                return false;
            case 'FLGTE':
                if (val2 && val2.length >= val1) {
                    return true;
                } else {
                    return false;
                }
            case 'FLLTE':
                if (val2 && val2.length <= val1) {
                    return true;
                } else {
                    return false;
                }
            case 'REGEX':
                if (val2 !== undefined) {
                    const regex = val2.match(new RegExp(val1));
                    if (regex !== null) {
                        return true;
                    }
                }
                return false;
            case 'CONTAINS':
                if (val2 && val1.some(v => val2.includes(v))) {
                    return true;
                } else {
                    return false;
                }
            case 'NOT CONTAINS':
                let check = false;
                for (const i of val1) {
                    const valArr = val2.toLowerCase().split(' ');
                    if (valArr.includes(i.toLowerCase())) {
                        check = true;
                        break;
                    }
                }
                return check;
            default:
                return false;
        }
    }

}
