import { Injectable } from '@angular/core';
import { ApiData } from '../models/api-data/api-data.model';
import { ApiDataQuery } from '../models/api-data/api-data.query';
import { ApiDataStore } from '../models/api-data/api-data.store';
import { AppData } from '../models/app-data/app-data.model';
import { AppDataQuery } from '../models/app-data/app-data.query';
import { AppDataStore } from '../models/app-data/app-data.store';

@Injectable({
    providedIn: 'root'
})
/**
 * class file mainly used to expose the store
 * data to the project side so that they can
 * access the store values
 */
export class NgCEEStoreService {
    constructor(
        private appDataQuery: AppDataQuery,
        private appDataStore: AppDataStore,
        private apiDataStore: ApiDataStore,
        private apiDataQuery: ApiDataQuery) { }

    public getAllAppData(): AppData[] {
        return this.appDataQuery.getAll();
    }

    public getIndividualAppData(id: string): AppData[] {
        return this.appDataQuery.getAll({
            filterBy: entity => entity.id === id
        });
    }

    public getAllApiData(): ApiData[] {
        return this.apiDataQuery.getAll();
    }

    public getIndividualApiData(id: string): ApiData[] {
        return this.apiDataQuery.getAll({
            filterBy: entity => entity.id === id
        });
    }

    public getAppStore() {
        return this.appDataStore.getValue().entities;
    }

    public getApiStore() {
        return this.apiDataStore.getValue().entities;
    }

}