import { Md5 } from 'ts-md5/dist/md5';
import { sha256 } from 'js-sha256';

export class WfeEncryptionUtil {

    constructor() { }

    getEncryptedData(encryptType, value) {
        switch (encryptType) {
            case 'MD5':
                const md5 = new Md5();
                return md5.appendStr(value).end();
            case 'SHA-256':
                return sha256(value);
            case 'Base64':
                return btoa(value);
            case 'DoubleBase64':
                return btoa(btoa(value));
            default:
                return value;
        }
    }
}
