/**
 * This is a util file that contains function that will be called when the api type is EXTERNAL.
 * This file reads the api config json gets the json for the specific event handler and forms the
 * header, requestBody, queryParameters, urlParameters.
 * Note:// When the type of api config is set to the EXTERNAL it will automatically use the hashed API KEY concept.
 */

import { HttpClient } from '@angular/common/http';
import { AppDataService } from '../models/app-data/app-data.service';
import { ApiDataService } from '../models/api-data/api-data.service';
import { FlatUnflat } from './flat-unflat-json';
import { ApiData } from '../models/api-data/api-data.model';
import { AppData } from '../models/app-data/app-data.model';
import { CommonUtil } from './common-util';
import { Injectable } from '@angular/core';
@Injectable({
    providedIn: 'root',
})
export default class InternalResponseUtil {
	constructor(
		public http: HttpClient,
		public appDataService: AppDataService,
		public apiDataService: ApiDataService
	) {
		this.flatAndNestUtil = new FlatUnflat(appDataService);
		this.commonUtil = new CommonUtil();
	}

	flatAndNestUtil: FlatUnflat;
	commonUtil: CommonUtil;

	/**
	 * this function save field data on response handler
	 * so that can consume on field load
	 * @required @param handler WFE Event handler
	 */
	saveResponseData(stepId) {

		const appStoreData = this.getAppData(stepId);
		this.saveApiData(appStoreData);
	}

	/**
	 * function returns an object which is the mapping of the  apiKey and the value in a field.
	 */
	private getAppData(stepId) {
		let obj = {};
		const appStoreData: AppData[] = this.appDataService.getAllAppStoreData();
		for (const data of appStoreData) {
			if (data.responseApiKey.length > 0 && data.stepId === stepId) {
				for (const key of data.responseApiKey) {
					if (key) {
						if (data.dataFormatSeparator) {
							// tslint:disable-next-line: no-unused-expression
							((key.includes(':::')) && (obj = {
								...this.checkForDateOrTimePicker(key.split(':::'),
									data.value.split(data.dataFormatSeparator)), ...obj
							}));
						}
						obj[key] = data.value;
					}
				}
			}
		}
		return obj;
	}

	private checkForDateOrTimePicker(keys, apiValue) {
		const obj = {};
		for (const [index, value] of keys.entries()) {
			obj[value] = apiValue[index] || '';
		}
		return obj;
	}

	private saveApiData(data) {
		// console.log(data)
		let obj = {};
		for (const key in data) {
			const value = data[key];
			const handlerName = this.commonUtil.getHandlerName(key);
			if (obj[handlerName]) {

			} else {
				const handlerData: ApiData = this.apiDataService.getApiDataByHandler(
					this.commonUtil.getHandlerName(key))
				obj[handlerName] = handlerData ? JSON.parse(JSON.stringify(handlerData.value)) : {};
			}
			obj[handlerName][key] = value;
		}
		// console.log(obj)
		for (const handlerName in obj) {
			const handlerData: ApiData = {
				apiKey: '',
				id: handlerName,
				apiUrl: handlerName,
				value: obj[handlerName]
			}
			const isPresent = this.apiDataService.getApiDataByHandler(handlerName);
			if (isPresent) {
				this.apiDataService.updateApiData(Object.assign({}, handlerData));
			} else {
				this.apiDataService.addApiData(Object.assign({}, handlerData));
			}
		}// setApiData
	}

}
