import { CommonModule, NgSwitch } from '@angular/common';
import { Component, OnInit, Inject, CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { MatDialogModule } from '@angular/material/dialog';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { MatDialogRef as MatDialogRef, MAT_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/dialog';
import { NgxImageZoomModule } from 'ngx-image-zoom';
import { DomSanitizePipe } from '../../pipes/dom-sanitize.pipe';
import { Dimensions, ImageCroppedEvent, ImageCropperComponent, ImageTransform, LoadedImage } from 'ngx-image-cropper';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
    selector: 'lib-image-crop-modal',
    templateUrl: './image-crop-modal.component.html',
    styleUrls: ['./image-crop-modal.component.scss'],
    standalone: true,
    imports: [NgSwitch, MatFormFieldModule, MatInputModule, CommonModule, MatDialogModule, NgxImageZoomModule, DomSanitizePipe, FormsModule, ImageCropperComponent],
    schemas:[CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA]
})
export class ImageCropModalComponent {

    maintainAspectRatio: any = false;
    cropperStaticWidth: any = 170;
    cropperStaticHeight: any = 170;
    cropperMinWidth: any = 170;
    cropperMinHeight: any = 170;
    onlyScaleDown: any = true;
    roundCropper: any = true;

    imageChangedEvent: any = '';
    croppedImage: any = '';
    canvasRotation = 0;
    rotation?: number;
    translateH = 0;
    translateV = 0;
    scale = 1;
    aspectRatio = 1/1;
    showCropper = false;
    containWithinAspectRatio = false;
    transform: ImageTransform = {
      translateUnit: 'px'
    };
    imageURL?: string;
    loading = false;
    allowMoveImage = true;
    hidden = false;
    ppFormat = 'png';
    additionalParameter: any;
    allowZoomImage: any = true;

    constructor(
        public dialogRef: MatDialogRef<ImageCropModalComponent>,
        private sanitizer: DomSanitizer,
        @Inject(MAT_DIALOG_DATA) public data: any) {
        // console.log(data);
        this.imageChangedEvent = data.message;
        this.additionalParameter = data.additionalParameter;
        this.maintainAspectRatio = this.additionalParameter && this.additionalParameter['CEE_PP_MaintainAspectRatio'] && this.additionalParameter['CEE_PP_MaintainAspectRatio'].toLowerCase() == 'true' ? true : false;
        this.aspectRatio = this.additionalParameter && this.additionalParameter['CEE_PP_AspectRatio'] ? this.additionalParameter['CEE_PP_AspectRatio'] : this.aspectRatio;
        this.ppFormat = this.additionalParameter && this.additionalParameter['CEE_PP_Format'] ? this.additionalParameter['CEE_PP_Format'] : this.ppFormat;
        this.cropperMinWidth = this.additionalParameter && this.additionalParameter['CEE_PP_CropperMinWidth'] ? Number(this.additionalParameter['CEE_PP_CropperMinWidth']) : this.cropperMinWidth;
        this.cropperMinHeight = this.additionalParameter && this.additionalParameter['CEE_PP_CropperMinHeight'] ? Number(this.additionalParameter['CEE_PP_CropperMinHeight']) : this.cropperMinHeight;
        this.roundCropper = this.additionalParameter && this.additionalParameter['CEE_PP_RoundCropper']  && this.additionalParameter['CEE_PP_RoundCropper'].toLowerCase() == 'true' ? true : false;
        this.allowMoveImage = this.additionalParameter && this.additionalParameter['CEE_AllowMoveImage']  && this.additionalParameter['CEE_AllowMoveImage'].toLowerCase() == 'true' ? true : false;
        this.allowZoomImage = this.additionalParameter && this.additionalParameter['CEE_AllowZoomImage']  && this.additionalParameter['CEE_AllowZoomImage'].toLowerCase() == 'true' ? true : false;
        // console.log("format: ",this.ppFormat)
        if (data.config && data.config.disableClose) {
            dialogRef.disableClose = data.config.disableClose;
        }
    }

    onPostiveClick(): void {
        this.dialogRef.close(true);
    }

    onNegativeClick(): void {
        this.dialogRef.close(false);
    }

    // #####################################
    
    imageCropped(event: ImageCroppedEvent) {
        this.croppedImage = this.sanitizer.bypassSecurityTrustUrl(event.objectUrl || event.base64 || '');
        // console.log(event);
    }

    imageLoaded() {
        this.showCropper = true;
        // console.log('Image loaded');
    }

    cropperReady(sourceImageDimensions: Dimensions) {
        // console.log('Cropper ready', sourceImageDimensions);
        this.loading = false;
    }

    loadImageFailed() {
        console.error('Load image failed');
    }

    rotateLeft() {
        this.loading = true;
        setTimeout(() => { // Use timeout because rotating image is a heavy operation and will block the ui thread
            this.canvasRotation--;
            this.flipAfterRotate();
        });
    }

    rotateRight() {
        this.loading = true;
        setTimeout(() => {
            this.canvasRotation++;
            this.flipAfterRotate();
        });
    }

    moveLeft() {
        this.transform = {
            ...this.transform,
            translateH: ++this.translateH
        };
    }

    moveRight() {
        this.transform = {
            ...this.transform,
            translateH: --this.translateH
        };
    }

    moveTop() {
        this.transform = {
            ...this.transform,
            translateV: ++this.translateV
        };
    }

    moveBottom() {
        this.transform = {
            ...this.transform,
            translateV: --this.translateV
        };
    }

    private flipAfterRotate() {
        const flippedH = this.transform.flipH;
        const flippedV = this.transform.flipV;
        this.transform = {
            ...this.transform,
            flipH: flippedV,
            flipV: flippedH
        };
        this.translateH = 0;
        this.translateV = 0;
    }

    flipHorizontal() {
        this.transform = {
            ...this.transform,
            flipH: !this.transform.flipH
        };
    }

    flipVertical() {
        this.transform = {
            ...this.transform,
            flipV: !this.transform.flipV
        };
    }

    resetImage() {
        this.scale = 1;
        this.rotation = 0;
        this.canvasRotation = 0;
        this.transform = {
            translateUnit: 'px'
        };
    }

    zoomOut() {
        this.scale -= .1;
        this.transform = {
            ...this.transform,
            scale: this.scale
        };
    }

    zoomIn() {
        this.scale += .1;
        this.transform = {
            ...this.transform,
            scale: this.scale
        };
    }

    toggleContainWithinAspectRatio() {
        this.containWithinAspectRatio = !this.containWithinAspectRatio;
    }

    updateRotation() {
        this.transform = {
            ...this.transform,
            rotate: this.rotation
        };
    }

    toggleAspectRatio() {
        this.aspectRatio = this.aspectRatio === 4 / 3 ? 16 / 5 : 4 / 3;
    }

    applyCropedImage() {
        // console.log("Cropped Image: ", this.croppedImage )
        this.dialogRef.close(this.croppedImage.changingThisBreaksApplicationSecurity);
    }

    cancelCropedImage() {
        // console.log("cancel Croped Image" )
        this.dialogRef.close();
    }
}
