import { Injectable } from '@angular/core';
import { SharedEventsServiceService } from './shared-events-service.service';

@Injectable({
  providedIn: 'root'
})
export class UserDataHandlerService {

  constructor(private sharedEventsService: SharedEventsServiceService) { }

  mandatoryData = [];

  setOrUpdateFieldData(apiKey: string, value: any) {
    window.sessionStorage.setItem(apiKey, value);
    // this.sharedEventsService.emitOnDataSetOrUpdated.emit({apiKey, value});
  }

  getFieldData(apiKey: string) {
    if (window.sessionStorage.getItem(apiKey)) {
      return window.sessionStorage.getItem(apiKey);
    } else {
      return '';
    }
  }

  removeSessionValues(apiKeys: string[]) {
    for (const key of apiKeys) {
      window.sessionStorage.removeItem(key);
    }
  }

  removeSessionValue(apiKey: string) {
    window.sessionStorage.removeItem(apiKey);
  }

  clearSessionValue() {
    window.sessionStorage.clear();
  }
}
