import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';
import { ApiData } from './api-data.model';

export interface ApiDataState extends EntityState<ApiData> { }

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'api-data' })
export class ApiDataStore extends EntityStore<ApiDataState, ApiData> {
    constructor() {
        super();
    }
}
